'use client';

import { useEffect, useState } from 'react';
import { 
  BarChart, 
  Bar, 
  XAxis, 
  YAxis, 
  Tooltip, 
  ResponsiveContainer,
  PieChart,
  Pie,
  Cell,
  Legend
} from 'recharts';

// กำหนด type สำหรับข้อมูลภูมิภาค
type RegionalData = {
  name: string;
  value: number;
};

type OrdainData = {
  projectName: string;
  implementationArea: { count: string; unit: string };
  policyExpansionArea: { count: string; unit: string };
  alcoholReduction?: { 
    total: string;
    templeEvents: string; 
    communityEvents: string 
  };
  modelMonks?: { count: string; unit: string };
  savings?: { 
    alcoholSavings: string; 
    expensePerMonk: string 
  };
  policy?: { count: string; unit: string };
  regionalData?: RegionalData[];
  rawData?: any[][];
};

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#8884D8', '#83A6ED', '#8DD1E1', '#A4DE6C'];

export default function OrdainStats() {
  const [data, setData] = useState<OrdainData | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchData() {
      try {
        setLoading(true);
        const response = await fetch('/api/ordain');
        const result = await response.json();
        
        console.log('API Response:', result); // เพิ่มเพื่อตรวจสอบข้อมูลที่ได้รับ
        
        if (response.ok) {
          setData(result.data);
          
          // ถ้ามี debug info ให้แสดงในคอนโซล
          if (result.debug) {
            console.log('Debug Info:', result.debug);
          }
        } else {
          console.error('API Error:', result);
          setError(result.error || 'เกิดข้อผิดพลาดในการดึงข้อมูล');
        }
      } catch (err) {
        console.error('Fetch Error:', err);
        setError('เกิดข้อผิดพลาดในการเชื่อมต่อกับเซิร์ฟเวอร์');
      } finally {
        setLoading(false);
      }
    }
    
    fetchData();
  }, []);

  if (loading) {
    return (
      <div className="flex justify-center items-center min-h-[400px]">
        <div className="animate-pulse flex flex-col items-center">
          <div className="w-12 h-12 border-4 border-t-blue-500 border-b-blue-700 rounded-full animate-spin"></div>
          <p className="mt-4 text-gray-600">กำลังโหลดข้อมูล...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="bg-red-50 border border-red-200 rounded-lg shadow-md p-6">
        <div className="flex items-center">
          <div className="mr-3 text-red-500">
            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
              <circle cx="12" cy="12" r="10"></circle>
              <line x1="12" y1="8" x2="12" y2="12"></line>
              <line x1="12" y1="16" x2="12.01" y2="16"></line>
            </svg>
          </div>
          <div>
            <h3 className="font-medium text-red-800">เกิดข้อผิดพลาด</h3>
            <p className="text-red-700">{error}</p>
          </div>
        </div>
      </div>
    );
  }

  if (!data) {
    return (
      <div className="bg-yellow-50 border border-yellow-200 rounded-lg shadow-md p-6">
        <div className="flex items-center">
          <div className="mr-3 text-yellow-500">
            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
              <path d="M10.29 3.86L1.82 18a2 2 0 0 0 1.71 3h16.94a2 2 0 0 0 1.71-3L13.71 3.86a2 2 0 0 0-3.42 0z"></path>
              <line x1="12" y1="9" x2="12" y2="13"></line>
              <line x1="12" y1="17" x2="12.01" y2="17"></line>
            </svg>
          </div>
          <div>
            <h3 className="font-medium text-yellow-800">ไม่พบข้อมูล</h3>
            <p className="text-yellow-700">ไม่สามารถดึงข้อมูลโครงการได้</p>
          </div>
        </div>
      </div>
    );
  }

  // สร้างข้อมูลสำหรับแผนภูมิแท่ง (กรณีที่มีข้อมูล alcoholReduction)
  const barChartData = data.alcoholReduction ? [
    { name: 'งานบวชทั้งหมด', value: parseInt(data.alcoholReduction.total) || 0 },
    { name: 'งานบวชในวัด', value: parseInt(data.alcoholReduction.templeEvents) || 0 },
    { name: 'งานบวชในชุมชน', value: parseInt(data.alcoholReduction.communityEvents) || 0 },
  ] : [];

  // สร้างข้อมูลสำหรับแผนภูมิวงกลม (กรณีที่มีข้อมูล alcoholReduction)
  const pieChartData = data.alcoholReduction ? [
    { name: 'งานบวชในวัด', value: parseInt(data.alcoholReduction.templeEvents) || 0 },
    { name: 'งานบวชในชุมชน', value: parseInt(data.alcoholReduction.communityEvents) || 0 },
  ] : [];

  // ฟังก์ชันสำหรับแปลงตัวเลขให้เป็นรูปแบบที่อ่านง่าย
  const formatNumber = (num: string) => {
    if (!num) return '0';
    return new Intl.NumberFormat('th-TH').format(parseInt(num));
  };

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-3xl font-bold text-center mb-8">{data.projectName}</h1>
      
      {/* แผนภูมิสรุปภาพรวม */}
      <div className="bg-white rounded-lg shadow-md overflow-hidden mb-10">
        <div className="px-6 py-4 border-b border-gray-200">
          <h3 className="text-xl font-semibold">ภาพรวมโครงการบวชสร้างสุข</h3>
        </div>
        <div className="px-6 py-4">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
            <div className="bg-blue-50 rounded-lg p-6 text-center">
              <p className="text-5xl font-bold text-blue-600">{formatNumber(data.implementationArea.count)}</p>
              <p className="text-sm text-blue-800 mt-2">พื้นที่ดำเนินการโครงการ</p>
            </div>
            <div className="bg-green-50 rounded-lg p-6 text-center">
              <p className="text-5xl font-bold text-green-600">
                {data.modelMonks ? formatNumber(data.modelMonks.count) : '0'}
              </p>
              <p className="text-sm text-green-800 mt-2">พระต้นแบบที่ผ่านกระบวนการ</p>
            </div>
            <div className="bg-yellow-50 rounded-lg p-6 text-center">
              <p className="text-5xl font-bold text-yellow-600">
                {data.alcoholReduction ? formatNumber(data.alcoholReduction.total) : '0'}
              </p>
              <p className="text-sm text-yellow-800 mt-2">งานบวชที่ลดการบริโภคแอลกอฮอล์</p>
            </div>
          </div>
        </div>
      </div>
      
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mb-10">
        {/* พื้นที่ดำเนินการ */}
        <div className="bg-gradient-to-br from-blue-50 to-blue-100 rounded-lg shadow-lg overflow-hidden">
          <div className="px-6 py-4 border-b border-blue-200">
            <h3 className="text-xl font-semibold text-blue-800">พื้นที่การดำเนินงาน</h3>
          </div>
          <div className="px-6 py-4">
            <div className="flex flex-col gap-6">
              <div className="bg-white rounded-lg p-6 shadow-inner">
                <h3 className="text-lg font-medium text-blue-700 mb-2">พื้นที่ดำเนินการโครงการ</h3>
                <div className="flex items-baseline">
                  <span className="text-4xl font-bold mr-2">{formatNumber(data.implementationArea.count)}</span>
                  <span className="text-gray-600">{data.implementationArea.unit}</span>
                </div>
              </div>
              <div className="bg-white rounded-lg p-6 shadow-inner">
                <h3 className="text-lg font-medium text-blue-700 mb-2">พื้นที่ขยายผลระดับนโยบาย</h3>
                <div className="flex items-baseline">
                  <span className="text-4xl font-bold mr-2">{formatNumber(data.policyExpansionArea.count)}</span>
                  <span className="text-gray-600">{data.policyExpansionArea.unit}</span>
                </div>
              </div>
            </div>
          </div>
          <div className="px-6 py-4 bg-blue-50 text-sm text-blue-600 border-t border-blue-200">
            พื้นที่ปลอดภัย ที่มีการควบคุมการดื่มเครื่องดื่มแอลกอฮอล์ในงานบุญประเพณี
          </div>
        </div>

        {/* จำนวนพระต้นแบบ */}
        <div className="bg-gradient-to-br from-amber-50 to-amber-100 rounded-lg shadow-lg overflow-hidden">
          <div className="px-6 py-4 border-b border-amber-200">
            <h3 className="text-xl font-semibold text-amber-800">พระต้นแบบ</h3>
          </div>
          <div className="px-6 py-4">
            <div className="bg-white rounded-lg p-6 shadow-inner mb-6">
              <h3 className="text-lg font-medium text-amber-700 mb-2">จำนวนพระต้นแบบที่ผ่านกระบวนการบวชสร้างสุข</h3>
              <div className="flex items-baseline">
                <span className="text-4xl font-bold mr-2">
                  {data.modelMonks ? formatNumber(data.modelMonks.count) : '0'}
                </span>
                <span className="text-gray-600">{data.modelMonks?.unit || 'รูป'}</span>
              </div>
              <p className="mt-3 text-sm text-gray-600">
                พระต้นแบบที่ยึดพระธรรมวินัย ประหยัด เรียบง่าย
              </p>
            </div>
            <div className="bg-white rounded-lg p-6 shadow-inner">
              <h3 className="text-lg font-medium text-amber-700 mb-2">นโยบาย/กติกา/สภาพแวดล้อม</h3>
              <div className="flex items-baseline">
                <span className="text-4xl font-bold mr-2">
                  {data.policy ? formatNumber(data.policy.count) : '0'}
                </span>
                <span className="text-gray-600">{data.policy?.unit || 'พื้นที่'}</span>
              </div>
              <p className="mt-3 text-sm text-gray-600">
                ที่เอื้อต่อการปรับเปลี่ยนค่านิยมการจัดงานบวช
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* ข้อมูลการลดการบริโภคเครื่องดื่มแอลกอฮอล์ */}
      {data.alcoholReduction && barChartData.length > 0 && (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mb-10">
          <div className="bg-gradient-to-br from-purple-50 to-purple-100 rounded-lg shadow-lg overflow-hidden">
            <div className="px-6 py-4 border-b border-purple-200">
              <h3 className="text-xl font-semibold text-purple-800">การลดการบริโภคเครื่องดื่มแอลกอฮอล์</h3>
            </div>
            <div className="px-6 py-4">
              <div className="h-72">
                <ResponsiveContainer width="100%" height="100%">
                  <BarChart data={barChartData} margin={{ top: 20, right: 30, left: 20, bottom: 5 }}>
                    <XAxis dataKey="name" />
                    <YAxis />
                    <Tooltip formatter={(value) => new Intl.NumberFormat('th-TH').format(value as number)} />
                    <Bar dataKey="value" fill="#8884d8">
                      {barChartData.map((entry, index) => (
                        <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                      ))}
                    </Bar>
                  </BarChart>
                </ResponsiveContainer>
              </div>
            </div>
            <div className="px-6 py-4 bg-purple-50 flex justify-between border-t border-purple-200">
              <span className="text-sm text-purple-600">งานบวชทั้งหมด: {formatNumber(data.alcoholReduction.total)} งาน</span>
              <span className="text-sm text-purple-600">งานบวชในวัด: {formatNumber(data.alcoholReduction.templeEvents)} งาน</span>
            </div>
          </div>

          <div className="bg-gradient-to-br from-pink-50 to-pink-100 rounded-lg shadow-lg overflow-hidden">
            <div className="px-6 py-4 border-b border-pink-200">
              <h3 className="text-xl font-semibold text-pink-800">สัดส่วนงานบวชต้นแบบ</h3>
            </div>
            <div className="px-6 py-4">
              <div className="h-72">
                <ResponsiveContainer width="100%" height="100%">
                  <PieChart>
                    <Pie
                      data={pieChartData}
                      cx="50%"
                      cy="50%"
                      labelLine={true}
                      label={({ name, percent }) => `${name}: ${(percent * 100).toFixed(0)}%`}
                      outerRadius={80}
                      fill="#8884d8"
                      dataKey="value"
                    >
                      {pieChartData.map((entry, index) => (
                        <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                      ))}
                    </Pie>
                    <Legend />
                    <Tooltip formatter={(value) => new Intl.NumberFormat('th-TH').format(value as number)} />
                  </PieChart>
                </ResponsiveContainer>
              </div>
            </div>
            <div className="px-6 py-4 bg-pink-50 text-sm text-pink-600 text-center border-t border-pink-200">
              งานบวชต้นแบบในชุมชน: {formatNumber(data.alcoholReduction.communityEvents)} งาน
            </div>
          </div>
        </div>
      )}

      {/* ข้อมูลการประหยัด */}
      {data.savings && (
        <div className="bg-gradient-to-br from-green-50 to-green-100 rounded-lg shadow-lg overflow-hidden mb-10">
          <div className="px-6 py-4 border-b border-green-200">
            <h3 className="text-xl font-semibold text-green-800">การประหยัดค่าใช้จ่าย</h3>
          </div>
          <div className="px-6 py-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="bg-white rounded-lg p-6 shadow-inner">
                <h3 className="text-lg font-medium text-green-700 mb-2">ประหยัดค่าเหล้าเบียร์</h3>
                <div className="flex items-baseline">
                  <span className="text-4xl font-bold mr-2">{formatNumber(data.savings.alcoholSavings)}</span>
                  <span className="text-gray-600">บาท</span>
                </div>
                <div className="mt-4 bg-green-100 p-3 rounded-lg">
                  <p className="text-sm text-green-800">คิดเป็นเงินที่ประหยัดได้จากการลดการซื้อเครื่องดื่มแอลกอฮอล์ในงานบวช</p>
                </div>
              </div>

              <div className="bg-white rounded-lg p-6 shadow-inner">
                <h3 className="text-lg font-medium text-green-700 mb-2">ประหยัดค่าใช้จ่ายต่อการบวชพระ</h3>
                <div className="flex items-baseline">
                  <span className="text-4xl font-bold mr-2">{formatNumber(data.savings.expensePerMonk)}</span>
                  <span className="text-gray-600">บาท</span>
                </div>
                <div className="mt-4 bg-green-100 p-3 rounded-lg">
                  <p className="text-sm text-green-800">ค่าใช้จ่ายที่ประหยัดได้จากการจัดงานบวชแบบเรียบง่าย</p>
                </div>
              </div>
            </div>
          </div>
          <div className="px-6 py-4 bg-green-50 text-sm text-green-600 text-center border-t border-green-200">
            สังคมไทยประหยัดค่าใช้จ่ายรวมกว่า {formatNumber((parseInt(data.savings.alcoholSavings) + parseInt(data.savings.expensePerMonk)).toString())} บาท
          </div>
        </div>
      )}

      {/* ข้อมูลสัดส่วนรายภูมิภาค */}
      {data.regionalData && data.regionalData.length > 0 && (
        <div className="bg-gradient-to-br from-teal-50 to-teal-100 rounded-lg shadow-lg overflow-hidden mb-10">
          <div className="px-6 py-4 border-b border-teal-200">
            <h3 className="text-xl font-semibold text-teal-800">สัดส่วนรายภูมิภาค</h3>
          </div>
          <div className="px-6 py-4">
            <div className="h-80">
              <ResponsiveContainer width="100%" height="100%">
                <PieChart>
                  <Pie
                    data={data.regionalData}
                    cx="50%"
                    cy="50%"
                    labelLine={true}
                    label={({ name, percent }) => `${name}: ${(percent * 100).toFixed(1)}%`}
                    outerRadius={120}
                    fill="#8884d8"
                    dataKey="value"
                  >
                    {data.regionalData.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                    ))}
                  </Pie>
                  <Legend layout="vertical" verticalAlign="middle" align="right" />
                  <Tooltip formatter={(value) => value} />
                </PieChart>
              </ResponsiveContainer>
            </div>
          </div>
          <div className="px-6 py-4 bg-teal-50 text-sm text-teal-600 text-center border-t border-teal-200">
            ข้อมูลการกระจายตัวของพื้นที่โครงการบวชสร้างสุขตามภูมิภาค
          </div>
        </div>
      )}

      {/* สรุปโครงการ */}
      <div className="bg-gradient-to-br from-indigo-50 to-indigo-100 rounded-lg shadow-lg overflow-hidden">
        <div className="px-6 py-4 border-b border-indigo-200">
          <h3 className="text-xl font-semibold text-indigo-800">สรุปผลสำเร็จของโครงการ</h3>
        </div>
        <div className="px-6 py-4">
          <div className="bg-white rounded-lg p-6 shadow-inner">
            <h3 className="text-lg font-medium text-indigo-700 mb-4">รูปธรรมความสำเร็จ</h3>
            <ul className="space-y-3">
              <li className="flex">
                <span className="mr-2 text-indigo-500">✓</span>
                <span>พื้นที่ดำเนินการโครงการบวชสร้างสุข <span className="font-semibold">{formatNumber(data.implementationArea.count)}</span> พื้นที่</span>
              </li>
              <li className="flex">
                <span className="mr-2 text-indigo-500">✓</span>
                <span>พื้นที่ขยายผลระดับนโยบาย <span className="font-semibold">{formatNumber(data.policyExpansionArea.count)}</span> พื้นที่</span>
              </li>
              {data.modelMonks && (
                <li className="flex">
                  <span className="mr-2 text-indigo-500">✓</span>
                  <span>พระต้นแบบที่ผ่านกระบวนการบวชสร้างสุข <span className="font-semibold">{formatNumber(data.modelMonks.count)}</span> รูป</span>
                </li>
              )}
              {data.alcoholReduction && (
                <li className="flex">
                  <span className="mr-2 text-indigo-500">✓</span>
                  <span>ลดการบริโภคเครื่องดื่มแอลกอฮอล์ในงานบวช <span className="font-semibold">{formatNumber(data.alcoholReduction.total)}</span> งาน</span>
                </li>
              )}
              {data.savings && (
                <li className="flex">
                  <span className="mr-2 text-indigo-500">✓</span>
                  <span>ประหยัดค่าใช้จ่ายรวม <span className="font-semibold">{formatNumber((parseInt(data.savings.alcoholSavings) + parseInt(data.savings.expensePerMonk)).toString())}</span> บาท</span>
                </li>
              )}
              {data.policy && (
                <li className="flex">
                  <span className="mr-2 text-indigo-500">✓</span>
                  <span>นโยบาย/กติกา/สภาพแวดล้อมที่เอื้อต่อการปรับเปลี่ยนค่านิยม <span className="font-semibold">{formatNumber(data.policy.count)}</span> พื้นที่</span>
                </li>
              )}
            </ul>
          </div>
        </div>
        <div className="px-6 py-4 bg-indigo-50 text-sm text-indigo-600 text-center border-t border-indigo-200">
          งานบวชสร้างสุขต้นแบบ "ยึดพระธรรมวินัย ประหยัด เรียบง่าย" (ปลอดเหล้า ปลอดปัจจัยเสี่ยง ลดผลกระทบความรุนแรง)
        </div>
      </div>
    </div>
  );
}